/** @format */

import React from "react"

export default function ByLocationDisplay({
	street,
	city,
	state,
	zip,
	radius,
	location,
}) {
	const [locationDisplay, setLocationDisplay] = React.useState(
		"123 Main Street, Wheeling WV 26003"
	)

	React.useEffect(() => {
		setLocationDisplay(
			location === ",  undefined"
				? "1110 Main Street, Wheeling WV 26003"
				: location
		)
	}, [location])
	return (
		<div>
			<span role='presentation'>
				{`Search within ${radius || "10"} miles of ${locationDisplay}`}
			</span>
		</div>
	)
}
