/** @format */
import React from "react"

import { checkboxStyle, labelStyle } from "../../../utils/styles"

export default function CulturalTrainingDate({
	displayFilters,
	setDisplayFilters,
}) {
	let { CulturalTrainingDate } = displayFilters
	function handleChangeDisplayFilters(e) {
		setDisplayFilters({
			...displayFilters,
			[e.target.name]: e.target.checked ? true : "",
		})
	}

	return (
		<>
			<div>
				<label className={labelStyle} htmlFor='CulturalTrainingDate'>
					Cultural Training Completed
					<input
						className={checkboxStyle}
						checked={CulturalTrainingDate}
						id='CulturalTrainingDate'
						name='CulturalTrainingDate'
						onChange={handleChangeDisplayFilters}
						type='checkbox'
					/>
				</label>
			</div>
		</>
	)
}
