/** @format */

import React from "react"
import SpecialtiesDropDown from "./specialtiesdropdown"
import FacilitiesDropDown from "./facilitiesdropdown"
import { labelStyle, checkboxStyle } from "../../../utils/styles"
import PostFilters from "./postsearchfilters"
import SpecialistPopup from "../../../components/popups/specialist"
import PcpPopup from "../../../components/popups/pcp"

// import { QueryClient, QueryClientProvider } from "react-query"
// import { ReactQueryDevtools } from "react-query/devtools"

export default function Providertypes({
	facs,
	searchFilters,
	setSearchFilters,
	specs,
}) {
	let { ProvType, specialty } = searchFilters

	function handleChangeSearchFilters(e) {
		setSearchFilters({ ...searchFilters, [e.target.name]: e.target.value })
	}

	return (
		<>
			<label htmlFor='chooseProvider-radios'>
				<h3>Browse doctors or places by type:</h3>
				<form
					id='chooseProvider-radios'
					name='ProvType'
					onChange={handleChangeSearchFilters}
					style={{
						display: "flex",
						flexFlow: "column ",
					}}
				>
					<div>
						<label htmlFor='provider-PCP' className={labelStyle}>
							PCP
							<PcpPopup />
							<input
								className={checkboxStyle}
								checked={ProvType === "PCP"}
								id='provider-PCP'
								name='ProvType'
								onChange={handleChangeSearchFilters}
								type='radio'
								value='PCP'
							/>
						</label>
					</div>
					<div>
						<label htmlFor='provider-Specialists' className={labelStyle}>
							Specialists
							<SpecialistPopup />
							<input
								className={checkboxStyle}
								checked={ProvType === "Specialists"}
								id='provider-Specialists'
								name='ProvType'
								onChange={handleChangeSearchFilters}
								type='radio'
								value='Specialists'
							/>
						</label>
					</div>
					<div>
						<label
							htmlFor='provider-HospitalsOrFacilities'
							className={labelStyle}
						>
							Hospitals or Facilities
							<input
								className={checkboxStyle}
								checked={ProvType === "Hospitals or Facilities"}
								id='provider-HospitalsOrFacilities'
								name='ProvType'
								onChange={handleChangeSearchFilters}
								type='radio'
								value='Hospitals or Facilities'
							/>
						</label>
					</div>
					<div>
						<label htmlFor='provider-All' className={labelStyle}>
							All
							<input
								className={checkboxStyle}
								checked={ProvType === ""}
								id='provider-All'
								name='ProvType'
								onChange={handleChangeSearchFilters}
								type='radio'
								value=''
							/>
						</label>
					</div>
				</form>
			</label>
			{ProvType == "Hospitals or Facilities" && (
				<FacilitiesDropDown
					facs={facs}
					handleChangeSearchFilters={handleChangeSearchFilters}
					specialty={specialty}
				/>
			)}
			{ProvType == "Specialists" && (
				<SpecialtiesDropDown
					specs={specs}
					handleChangeSearchFilters={handleChangeSearchFilters}
					specialty={specialty}
				/>
			)}
		</>
	)
}
