/** @format */

import React from "react"

import { selectStyles, labelStyle } from "../../../utils/styles.js"

export default function SpecialtiesDropDown({
	specialty,
	handleChangeSearchFilters,
	specs,
}) {
	return (
		<>
			<div>
				<label className={labelStyle} htmlFor='Specialities'>
					Specialities
					<select
						className={selectStyles}
						onChange={handleChangeSearchFilters}
						type='select'
						name='specialty'
						id='Specialities'
						value={specialty || ""}
					>
						<option value=''>Specialities</option>
						{specs &&
							specs?.map((specialty, idx) => (
								<option
									title={specialty.Text}
									key={`${specialty.Text}_${idx}_${specialty.ID}`}
									value={specialty.Text}
								>
									{specialty.Text}
								</option>
							))}
					</select>
				</label>
			</div>
		</>
	)
}
