/** @format */

import axios from "axios"
import { useState, useEffect } from "react"
export function useGeocodeAddress(address) {
	// 1109+N+Highland+St,+Arlington+VA
	// let tempaddress = "31 FoxFire Drive, Asheville, NC"
	// "31+FoxFire+Drive,+Asheville,+NC"

	const [addressQuery, setAddressQuery] = useState()
	const [latLon, setLatLon] = useState({ lat: "", lon: "" })
	let { lat, lon } = latLon

	useEffect(() => {
		address && setAddressQuery(address.trim().split(" ").join("+"))
	}, [])

	useEffect(() => {
		axios
			.get(
				`https://api.geocod.io/v1.6/geocode?q=${addressQuery}&api_key=${process.env.REACT_APP_FINDADOC_GEOCODIO}`
			)
			.then((res) =>
				setLatLon({
					lat: res.data.results[0].location.lat,
					lon: res.data.results[0].location.lng,
				})
			)
	}, [addressQuery])

	return [latLon]
}

