import devMapImg from "./1037x578.png"
import { useState, useEffect } from "react"
import { useWindowSize } from "./useWindowSize"

export function useStaticMapBox(latLon) {
	const [w, h] = useWindowSize()
	const [map, setMap] = useState()

	useEffect(() => {
		console.log("environment", process.env.NODE_ENV)
		let mapWidth = (w * 0.9).toFixed(0)
		process.env.NODE_ENV === "development"
			? setMap(devMapImg)
			: latLon?.lat &&
			  setMap(
					`https://api.mapbox.com/styles/v1/mapbox/streets-v11/static/pin-l-doctor+545454(${
						latLon.lon
					},${latLon.lat})/${latLon.lon},${latLon.lat},15/${
						!w ? 700 : mapWidth > 1280 ? 1280 : mapWidth
					}x${h ? (h * 0.5).toFixed(0) : 300}?access_token=${
						process.env.REACT_APP_FINDADOC_MAPBOX_TOKEN_STATICMAPS
					}`
			  )
	}, [latLon])

	return [map]
}
