/** @format */
import React, { useState, useEffect } from "react";
import { Route, Link } from "react-router-dom";
import FindAProvider from "../../pages/findaprovider";
import Search from "../../pages/searchresults/search";
import Filter from "../../pages/searchresults/filter";
import Results from "../../pages/searchresults/results";
import { stateUSPSCodesDict } from "../../utils/stateUSPScodes";

import axios from "axios";
import { createSearchFiltersQuery } from "../../utils/createSearchFiltersQuery";
import { useBrowserGPS, GeolocationMode } from "../../utils/useBrowserGPS";

import "./index.css";
import SearchResults from "../../pages/searchresults";
import ProviderDetails from "../../pages/providerdetails";
import { useReverseGeocode } from "../../utils/useReverseGeocode";

export default function Main({
  facsLIST,
  setHeaderValue,
  specsLIST,
  searchResults__mock
}) {
  const [url, setUrl] = useState();
  const [facs, setFacs] = useState(facsLIST);
  const [specs, setSpecs] = useState([]);

  const [searchFilters, setSearchFilters] = useState({
    city: "",
    provname: "",
    network: "",
    Participate: "",
    plan: "",
    ProvType: "",
    Radius: "",
    address1: "",
    state: "",
    zip: ""
  });
  const [displayFilters, setDisplayFilters] = useState({
    AcceptPT: "Yes",
    Accred: "",
    AccredDate: "",
    BoardCert: "",
    CulturalTrainingDate: "",
    FLang: "",
    Gender: "",
    NotHandicapAccessible: ""
  });
  const [dropdowns, setDropdowns] = useState({
    facilityDD: [],
    specialtyDD: []
  });
  const [saved, setSaved] = useState([]);
  const [found, setFound] = useState(searchResults__mock);
  const [coordsObj, setCoordsObj] = useState();

  function submitSearch(e) {
    e.preventDefault();
    axios
      .post(url)
      .then((res) =>
        setFound(
          res.data.SearchResults.map(({ ExtensionData, ...rest }) => rest)
        )
      )
      .catch((err) => console.error({ err }));
  }

  function handleSaveProv(p) {
    saved.includes(p)
      ? setSaved(([p, ...rest]) => rest)
      : setSaved([p, ...saved]);
  }

  const { position, error } = useBrowserGPS(GeolocationMode.SINGLE);

  useEffect(() => {
    if (position?.coords != null && position?.coords != undefined) {
      setCoordsObj(position.coords);
    }
  }, [position]);

  const [address, addressOBJ] = useReverseGeocode(coordsObj);

  //loadDROPDOWN__specialties /
  const [addressArr, setAddressArr] = useState();

  // useEffect(() => {
  // 	console.log({ searchFilters })
  // }, [address, addressArr, searchFilters])

  const [state, dispatch] = React.useReducer({ address: "" });
  useEffect(() => {
    setAddressArr(address?.split(","));
  }, [address]);

  useEffect(() => {
    let address1Str, cityStr, stateZipArr, stateZipStr, stateStr, zipStr;

    if (addressArr) {
      {
        /** address="790 Grandview Street, Wheeling, West Virginia 26003, United States"
	 addressArr=["790 Grandview Street", " Wheeling", " West Virginia 26003", " United States"]
	 stateZipStr=" West Virginia 26003"
	 stateZipArr=["West", "Virginia", "26003"]*/
      }
      address1Str = addressArr[0];
      cityStr = addressArr[1];
      stateZipStr = addressArr[2];
      stateZipArr = stateZipStr.trim().split(" ");
      // stateStr =
      // 	stateZipArr.length == 3
      // 		? stateZipArr[0] + " " + stateZipArr[1]
      // 		: stateZipArr[0]
      // zipStr = stateZipArr.length == 3 ? stateZipArr[2] : stateZipArr[1]
      if (stateZipArr.length == 3) {
        setSearchFilters({
          ...searchFilters,
          address1: address1Str,
          city: cityStr,
          state: stateUSPSCodesDict[`${stateZipArr[0]} ${stateZipArr[1]}`],
          zip: stateZipArr[2]
        });
      } else if (stateZipArr.length == 2) {
        setSearchFilters({
          ...searchFilters,
          address1: address1Str,
          city: cityStr,
          state: stateUSPSCodesDict[stateZipArr[0]],
          zip: stateZipArr[1]
        });
      }
    }
  }, [addressArr]);

  useEffect(() => {
    if (process.env.NODE_ENV === "development") {
      // axios
      // 	.get(`/ProviderSearch/getSearchFilters?filterType=FacilityTypes`)
      // 	.then(({ data }) => setFacs(data))
      // 	.catch((err) => console.error(err))
      setSpecs(specsLIST);
    }
    if (process.env.NODE_ENV === "production") {
      axios
        .get(
          `/ProviderSearch/getSearchFilters?filterType=Provider_Web_Specialties`
        )
        .then(({ data }) => setSpecs(data))
        .catch((err) => console.error(err), setSpecs(specsLIST));
    }
  }, []);

  //#region 	loadDROPDOWN__facilities

  useEffect(() => {
    if (process.env.NODE_ENV === "development") {
      // axios
      // 	.get(`/ProviderSearch/getSearchFilters?filterType=FacilityTypes`)
      // 	.then(({ data }) => setFacs(data))
      // 	.catch((err) => console.error(err))
    }
    if (process.env.NODE_ENV === "production") {
      axios
        .get(`/ProviderSearch/getSearchFilters?filterType=FacilityTypes`)
        .then(({ data }) => setFacs(data))
        .catch((err) => console.error(err), setFacs(facsLIST));
    }
  }, []);

  useEffect(() => {
    /***********************************************************************
     * set url if specialty || facility || any of the searchFilters change *
     ***********************************************************************/

    setUrl(
      `/ProviderSearch/searchProviders${createSearchFiltersQuery(
        searchFilters
      )}`
    );
  }, [searchFilters]);

  return (
    <div className="main">
      <Route exact path="/">
        <FindAProvider setHeaderValue={setHeaderValue} />
      </Route>
      <Route path="/searchresults">
        <SearchResults
          setHeaderValue={setHeaderValue}
          displayFilters={displayFilters}
          facs={facs}
          found={found}
          handleSaveProv={handleSaveProv}
          saved={saved}
          searchFilters={searchFilters}
          setDisplayFilters={setDisplayFilters}
          setSearchFilters={setSearchFilters}
          specs={specs}
          submitSearch={submitSearch}
          address={address || "1110 Main St Wheeling, WV 26003"}
        />

        <Route path="/searchresults/saved">
          SAVED
          <SearchResults
            setHeaderValue={setHeaderValue}
            displayFilters={displayFilters}
            facs={facs}
            found={found}
            handleSaveProv={handleSaveProv}
            saved={saved}
            searchFilters={searchFilters}
            setDisplayFilters={setDisplayFilters}
            setSearchFilters={setSearchFilters}
            specs={specs}
            submitSearch={submitSearch}
            address={address || "1110 Main St Wheeling, WV 26003"}
          />
        </Route>
      </Route>
      <Route path="/providerdetails/:provID">
        <ProviderDetails
          setHeaderValue={setHeaderValue}
          found={found}
          handleSaveProv={handleSaveProv}
        />
      </Route>
    </div>
  );
}
