/** @format */

import React from "react"

export default function Print() {
	function printResults(e) {
		e.preventDefault()
		window.print()
	}
	return (
		<div className='print' onClick={printResults}>
			<span role='img' aria-label='print results'>
				
			</span>
			<button>Print</button>
		</div>
	)
}
