/** @format */

import React from "react"
import Downshift from "downshift"
import { useMapboxGeocoder } from "./useMapboxGeocoder"
import { css } from "@emotion/css"

const searchLocation_inputsInnerBoxSearchBar = css`
	width: 100%;
	align-items: center;
	display: flex;
	font-size: 2rem;
	gap: 1rem;
	label {
		font-weight: bold;
		display: block;
	}
	.searchlocation_inputsInnersubbox {
		width: 100%;
		display: flex;
		input {
			height: 3rem;
			width: 100%;
		}
	}
`

export default function AutocompleteSearch() {
	const [selectedPlace, setSelectedPlace] = React.useState(null)
	const {
		error,
		isLoading,
		data: mapboxResults,
		fetchMapboxResults,
	} = useMapboxGeocoder()

	function handleInputChange(event) {
		if (!event.target.value) {
			return
		}
		fetchMapboxResults(event.target.value)
	}

	function handleDownshiftOnChange(selectedResult) {
		setSelectedPlace(selectedResult)
	}

	return (
		<Downshift
			onChange={handleDownshiftOnChange}
			itemToString={(item) => (item ? item.place_name : "")}
		>
			{({
				selectedItem,
				getInputProps,
				getItemProps,
				getLabelProps,
				getMenuProps,
				getToggleButtonProps,
				clearSelection,
				highlightedIndex,
				isOpen,
				inputValue,
			}) => {
				return (
					<div className={searchLocation_inputsInnerBoxSearchBar}>
						<label {...getLabelProps()}>Search</label>
						<div className='searchlocation_inputsInnersubbox'>
							<input
								{...getInputProps({
									placeholder: "ex. 123 Main Street, Wheeling WV 26003",
									onChange: handleInputChange,
								})}
							/>
							{selectedItem && (
								<button onClick={clearSelection} aria-label='clear selection'>
									X
								</button>
							)}
						</div>

						{isOpen && (
							<ul
								style={{
									padding: 0,
									marginTop: 0,
									position: "absolute",
									backgroundColor: "white",
									width: "100%",
									maxHeight: "20rem",
									overflowY: "auto",
									overflowX: "hidden",
									outline: "0",
									transition: "opacity .1s ease",
									borderRadius: "0 0 .28571429rem .28571429rem",
									boxShadow: "0 2px 3px 0 rgba(34,36,38,.15)",
									borderColor: "#96c8da",
									borderTopWidth: "0",
									borderRightWidth: 1,
									borderBottomWidth: 1,
									borderLeftWidth: 1,
									borderStyle: "solid",
								}}
								{...getMenuProps({ isOpen })}
							>
								{isLoading && <div disabled>Loading...</div>}
								{error && <div disabled>Error!</div>}
								{!isLoading && !error && !mapboxResults.length && (
									<div>No results returned</div>
								)}
								{inputValue &&
									mapboxResults.length > 0 &&
									mapboxResults.slice(0, 10).map((item, index) => (
										<div
											{...getItemProps({
												key: index,
												index,
												item,
												isActive: highlightedIndex === index,
												isSelected: selectedItem === item,
											})}
											style={{
												backgroundColor:
													highlightedIndex === index ? "lightgray" : "white",
												fontWeight: selectedItem === item ? "bold" : "normal",
											}}
										>
											<p>{item.place_name}</p>
										</div>
									))}
							</ul>
						)}
					</div>
				)
			}}
		</Downshift>
	)
}
