/** @format */

import React from "react";

export default function Pagination({ setPage, page, lastPage }) {
  return (
    <>
      {
        // page > 1  &&
        <nav
          className="pagination is-medium"
          role="navigation"
          aria-label="pagination"
        >
          {page > 0 && (
            <button
              className="pagination-previous"
              onClick={() => setPage(page - 1)}
              aria-label={`Go to page ${page}`}
            >
              Previous
            </button>
          )}
          {lastPage > page + 1 && (
            <button
              className="pagination-next"
              onClick={() => setPage(page + 1)}
              aria-label={`Go to page (${page + 1})`}
            >
              Next page
            </button>
          )}{" "}
          <ul className="pagination-list">
            {page > 1 && (
              <>
                <li>
                  <button
                    className="pagination-link"
                    onClick={() => setPage(0)}
                    aria-label={`Goto page 1`}
                  >
                    1
                  </button>
                </li>
                <li>
                  <span className="pagination-ellipsis">&hellip;</span>
                </li>
              </>
            )}

            {page > 0 && (
              <li>
                <button
                  className="pagination-link"
                  onClick={() => setPage(page - 1)}
                  aria-label={`Goto page ${page}`}
                >
                  {page}
                </button>
              </li>
            )}

            <li>
              <button
                className="pagination-link is-current"
                aria-label={`Page ${page + 1}`}
                aria-current="page"
              >
                {page + 1}
              </button>
            </li>

            {page < lastPage && (
              <li>
                <button
                  className="pagination-link"
                  onClick={() => setPage(page + 1)}
                  aria-label={`Goto page ${page + 2}`}
                >
                  {page + 2}
                </button>
              </li>
            )}

            {page + 1 < lastPage && (
              <li>
                <button
                  className="pagination-link"
                  onClick={() => setPage(page + 2)}
                  aria-label={`Goto page ${page + 3}`}
                >
                  {page + 3}
                </button>
              </li>
            )}

            {page + 2 < lastPage && (
              <li>
                <button
                  className="pagination-link"
                  onClick={() => setPage(page + 3)}
                  aria-label={`Goto page ${page + 4}`}
                >
                  {page + 4}
                </button>
              </li>
            )}

            {page + 3 < lastPage && (
              <>
                <li>
                  <span className="pagination-ellipsis">&hellip;</span>
                </li>
                <li>
                  <button
                    className="pagination-link"
                    onClick={() => setPage(lastPage)}
                    aria-label={`Goto page ${lastPage}`}
                  >
                    {lastPage}
                  </button>
                </li>
              </>
            )}
          </ul>
        </nav>
      }
    </>
  );
}
