/** @format */
import { BsFillCaretDownFill, BsCaretDownFill } from "react-icons/bs"
import { useFindXX } from "../../../utils/useFindXX"
import { selectStyles } from "../../../utils/styles.js"

export default function BoardCertifications({
	displayFilters,
	setDisplayFilters,
	found,
}) {
	const { BoardCert } = displayFilters
	const [boardCerts, certedProvs, certedProvsCount] = useFindXX(
		found,
		"BoardCert"
	)

	function handleChangeDisplayFilters(e) {
		setDisplayFilters({
			...displayFilters,
			[e.target.name]: e.target.value,
		})
	}
	return (
		<>
			<select
				className={selectStyles}
				onChange={handleChangeDisplayFilters}
				type='select'
				name='BoardCert'
				id='Board Certifications'
				value={BoardCert || ""}
			>
				<option value=''>Board Certification {certedProvsCount || ""}</option>
				{boardCerts &&
					boardCerts?.map((cert, idx) => (
						<option
							title={cert}
							key={`${cert}_${idx}`}
							value={cert}
							style={{ display: "flex", justifyContent: "space-between" }}
						>
							{cert}
							&nbsp;&nbsp;
							{certedProvs.filter((p) => p.BoardCert === cert).length}
						</option>
					))}
			</select>
		</>
	)
}
