/** @format */

import React from "react"
import {
	AcceptNewPatients,
	Address,
	Participate,
	BtnDetails,
	ProvName,
	BtnSaveProv,
	Phone,
	Specialty,
} from "./index.js"

export function ProviderCard({
	provider,
	idx,
	handleSaveProv,
	routeToSingleProvider,
}) {
	return (
		<>
			<div
				className={`section ${
					idx % 2 == 0 ? "has-background-success-light" : ""
				}`}
			>
				<div
					className={`box ${
						idx % 2 !== 0 ? "has-background-success-light" : ""
					}`}
				>
					<div className=' '>
						<div className='columns'>
							<div className='column is-one-third-desktop is-full-mobile'>
								<ProvName Name={provider?.Name || ""} />
								<Address
									Address1={provider?.Address1 || ""}
									Address2={provider?.Address2 || ""}
									City={provider?.City || ""}
									State={provider?.State || ""}
									Zip={provider?.Zip || ""}
								/>
								<Phone Phone={provider?.Phone || ""} />
							</div>

							<div className='column is-one-third-desktop is-half-tablet'>
								<Participate Participate={provider?.Participate || ""} />
								<Specialty Specialty={provider?.Specialty || ""} />
							</div>
							<div className='column is-one-third-desktop is-full-mobile'>
								<div className='content  buttons has-addons pl-0 pr-0  '>
									<AcceptNewPatients AcceptPT={provider?.AcceptPT || ""} />
								</div>
								<BtnDetails
									routeToSingleProvider={routeToSingleProvider}
									provider={provider}
								></BtnDetails>
								<BtnSaveProv
									provider={provider}
									handleSaveProv={handleSaveProv}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	)
}
