/** @format */

import { useEffect, useState } from "react"
import { useActiveDisplayFilters } from "./useActiveDisplayFilters"

function isSubset(resultsObj, filtersObj) {
	//if all values match return true
	return Object.keys(filtersObj).every((ele) => {
		if (typeof filtersObj[ele] === "object") {
			return isSubset(resultsObj[ele], filtersObj[ele])
		}
		// if culturalTraining checkbox is checked, then provider's with nullish (false, null,emptystring, undefined) culturaltrainingdate values are filtered out
		if (
			ele === "CulturalTrainingDate" &&
			filtersObj[ele] === true &&
			resultsObj[ele].length > 1
		) {
			return true
		}
		//if value @ obj1[key] === value @ obj2[key] return true
		return filtersObj[ele] === resultsObj[ele]
	})
}

export function useFilterDisplayedResults(found, displayFilters) {
	const [activeDisplayFilters] = useActiveDisplayFilters(displayFilters)
	const [displayed, setDisplayed] = useState([])

	useEffect(() => {
		found &&
			setDisplayed(
				found.filter((providerObj) =>
					isSubset(providerObj, activeDisplayFilters)
				)
			)
	}, [activeDisplayFilters, found])

	return [displayed]
}
