/** @format */

import axios from "axios"
import { useState, useEffect } from "react"

export function useReverseGeocode(coords) {
	const [coordsQuery, setCoordsQuery] = useState()
	const [address, setAddress] = useState()

	useEffect(() => {
		setCoordsQuery(coords)
	}, [coords])

	useEffect(() => {
		if (coordsQuery) {
			axios
				.get(
					`https://api.mapbox.com/geocoding/v5/mapbox.places/${coordsQuery.longitude},${coordsQuery.latitude}.json?access_token=${process.env.REACT_APP_FINDADOC_MAPBOX_TOKEN_REVERSE_GEOCODE}`
				)
				.then((res) => {
					let temp = res

					return setAddress(temp.data)
				})
		}
	}, [coordsQuery])

	return [address?.features[0]?.place_name, address]
}

