/** @format */

import React from "react";

import { labelStyle, selectStyles } from "../../../utils/styles.js";
export default function FacilitiesDropDown({
  specialty,
  handleChangeSearchFilters,
  facs
}) {
  return (
    <>
      <div>
        <label className={labelStyle} htmlFor="Facilities">
          Facilities
        </label>
        <select
          className={selectStyles}
          onChange={handleChangeSearchFilters}
          type="select"
          name="specialty"
          id="Facilities"
          value={specialty || ""}
        >
          <option value="">Facilities</option>
          {facs &&
            facs?.map((facility, idx) => (
              <option
                title={facility.Text ?? facility.facility}
                key={`${facility.Text ?? facility.facility}_${idx}_${
                  facility?.ID || idx
                }`}
                value={facility.Text ?? facility.facility}
              >
                {facility.Text ?? facility.facility}
              </option>
            ))}
        </select>
      </div>
    </>
  );
}
