/** @format */

export function createSearchFiltersQuery(searchFilters) {
	let qString = `?`

	searchFilters &&
		Object.entries({
			...searchFilters,
		}).map(([k, v]) => {
			if (k !== "provname" && v !== "") {
				qString += `&${k}=${v}`
			}

			if (k == "provname" && v !== "") {
				qString += `&name=${v}`
			}
		})

	return qString
}
