/** @format */

import React from "react"
import FAQPopup from "../faq-popup"

export default function PcpPopup() {
	return (
		<FAQPopup
			headerTextProp='What is a Primary Car Physician?'
			titleTextProp='What is a Primary Car Physician?'
			closeBtnTextProp='Got it!'
			contentTextProp={
				<div className='modal-body'>
					<p>
						A primary care physician (PCP) helps patients maintain overall
						health by focusing on preventive care, instead of specializing in a
						specific area of medicine. Included in this category are a family
						practice or general practice. A family practitioner is qualified to
						care for the entire family. A family practitioner can be
						board-certified and have training in a variety of subjects including
						obstetrics and gynecology, pediatrics, internal medicine and
						psychiatry.
					</p>
					<p>
						An internist in internal medicine can diagnose and treat disease
						with medicine. An internist is not a surgeon. There are several
						sub-specialties an internist can have, including: specializing in a
						particular organ, like the lungs or the kidneys; a particular
						disease, like diabetes; or a particular age group, like the elderly.
					</p>
					<p>
						A pediatrician specializes in the overall well-being of children.
						Most pediatricians treat children from birth until adolescence or
						about 14 years old. Pediatricians can have subspecialties such as
						surgery or pediatric cardiology.
					</p>
					<p>Non-physician primary care practitioner can be selected as PCP:</p>
					<ul>
						<li>Certified Nurse Practitioner (CNP)</li>
						<li>Certified Nurse Midwife (CNM)</li>
						<li>West Virginia Certified Physician Assistants (PA-C)</li>
					</ul>
					<p>
						<b>You may change your PCP no more than once per calendar month.</b>
					</p>
				</div>
			}
		></FAQPopup>
	)
}
