/** @format */

import { BsArrowClockwise } from "react-icons/bs"
import React, { useEffect, useState } from "react"

import { initialState } from "../../../store/state/initialstate"
import { btnStyle, labelStyle } from "../../../utils/styles"
export default function ClearFilters({ setDisplayFilters, setSearchFilters }) {
	let { displayFilters, searchFilters } = initialState
	function resetFilters() {
		setDisplayFilters(displayFilters)
		setSearchFilters(searchFilters)
	}

	return (
		<span>
			<label className={labelStyle}>
				<p>reset filters</p>
				<button className={btnStyle} onClick={resetFilters}>
					<BsArrowClockwise />
				</button>
			</label>
		</span>
	)
}
