/** @format */

import React from "react"
import { checkboxStyle, labelStyle } from "../../../utils/styles"
export default function HandicapAccessible({
	displayFilters,
	setDisplayFilters,
}) {
	let { NotHandicapAccessible } = displayFilters

	function handleCheckBox(e) {
		setDisplayFilters({
			...displayFilters,
			[e.target.name]: !e.target.checked,
		})
	}

	return (
		<>
			<div>
				<label className={labelStyle} htmlFor='Handicap Accessible'>
					Handicap Accessible
					<input
						className={checkboxStyle}
						onChange={handleCheckBox}
						type='checkbox'
						name='NotHandicapAccessible'
						id='Handicap Accessible'
						checked={!NotHandicapAccessible}
					/>
				</label>
			</div>
		</>
	)
}
