/** @format */

import { useFindXX } from "../../../utils/useFindXX"
import { selectStyles } from "../../../utils/styles.js"
import LanguagePopup from "../../../components/popups/languages"

export default function FLang({ displayFilters, setDisplayFilters, found }) {
	const [fLangs, fluentProvs, fLangProvsCount] = useFindXX(found, "FLang")

	function handleChangeDisplayFilters(e) {
		setDisplayFilters({
			...displayFilters,
			[e.target.name]: e.target.value,
		})
	}

	return (
		<select
			className={selectStyles}
			name='FLang'
			value={displayFilters.FLang || ""}
			onChange={handleChangeDisplayFilters}
		>
			<option style={{ display: "flex", justifyContent: "space-between" }}>
				Spoken Languages {fLangProvsCount || ""}
			</option>
			{fLangs &&
				fLangs?.map((lang, idx) => (
					<option key={`${lang}_${idx}`} name='FLang' title={lang} value={lang}>
						{lang}
						{fluentProvs.filter((p) => p.FLang === lang).length}
					</option>
				))}
		</select>
	)
}
