/** @format */

import { css } from "@emotion/css"
import { BsQuestionSquare } from "react-icons/bs"

import {
	useDialogState,
	Dialog,
	DialogBackdrop,
	DialogDisclosure,
} from "reakit/Dialog"

const backdropStyles = css`
	perspective: 800px;
	transition: opacity 250ms ease-in-out;
	opacity: 0;
	&[data-enter] {
		opacity: 1;
	}
`

const dialogStyles = css`
	display: flex;
	flex-flow: column;
	font-size: 1.4rem;
	gap: 1vmin 1.5vw;
	opacity: 0;
	overflow-y: overlay;
	padding: 1vw;
	transform-origin: top center;
	transform: translate3d(-50%, -10%, 0) rotateX(90deg);
	transition: opacity 250ms ease-in-out, transform 250ms ease-in-out;
	width: clamp(300px, 80vw, 700px);
	&[data-enter] {
		opacity: 1;
		transform: translate3d(-50%, 0, 0);
	}
	a {
		color: #51791f;
		text-decoration: none;
	}
	hr {
		width: 100%;
		margin: 0;
		padding: 0;
	}
	h3 {
		color: rgba(0, 175, 65, 1);
		margin: 0;
		padding: 0;
	}
`
const dialogDisclosureStyles = css`
	background:white !important;	

	color: green !important;
	height: auto;
	width:auto;
	padding:0;
	padding-right: 1rem;
	transition: background 250ms ease-in-out,
				background-color 250ms ease-in-out,
				color 250ms ease-in-out;
	 

	}
`
const questionBtnStyles = css`
	background: transparent !important;
	color: rgba(20, 200, 10, 0.75) !important;

	font-size: 1.6rem;
	&:hover {
		background: rgba(20, 200, 10, 0.75) !important;
		color: white !important;
	}
`

const popupbtnStyles = css`
	align-items: center;
	appearance: none;
	background-color: rgba(10, 150, 10, 1);
	border-radius: 4px;
	border: 1px solid rgba(10, 150, 10, 1);
	color: white;
	cursor: pointer;
	display: flex;
	gap: 0;
	height: 3rem;
	justify-content: center;
	margin: 0;
	outline: 0;
	padding: 0;
	width: 3rem;
	&:hover {
		background-color: white;

		color: rgba(10, 150, 10, 1);
		border: rgba(20, 200, 10, 1) solid 1px;
	}
	&:active {
		color: white;
		background: rgba(20, 200, 10, 0.75);
	}
`
export default function FAQPopup({
	children,
	closeBtnTextProp,
	contentTextProp,
	headerTextProp,
	linkTextProp,
	titleTextProp,
}) {
	const dialog = useDialogState({ animated: true })
	return (
		<>
			<DialogDisclosure className={dialogDisclosureStyles} {...dialog}>
				{children && <p style={{ fontSize: "1.6rem" }}>{children}</p>}
				<BsQuestionSquare
					className={popupbtnStyles}
					style={{
						width: 25,
						height: 25,
						border: "none",
						margin: 0,
						padding: 0,
					}}
					title={titleTextProp}
				/>
			</DialogDisclosure>
			<DialogBackdrop {...dialog} className={backdropStyles}>
				<Dialog
					{...dialog}
					aria-label={headerTextProp || "popup information box"}
					className={dialogStyles}
				>
					<h3>{headerTextProp || ""}</h3>
					<hr />
					{contentTextProp}
					<hr />
					<div style={{ display: "flex", justifyContent: "flex-end" }}>
						<button
							style={{
								backgroundColor: "rgba(0,175,65,1)",
								fontSize: "1.6rem",
								color: "white ",
							}}
							onClick={dialog.hide}
						>
							{closeBtnTextProp || "Close"}
						</button>
					</div>
				</Dialog>
			</DialogBackdrop>
		</>
	)
}
