/** @format */

import React from "react"
import { checkboxStyle, labelStyle } from "../../../utils/styles"

export default function AcceptPT({ displayFilters, setDisplayFilters }) {
	const [isChecked, setIsChecked] = React.useState(false)
	let { AcceptPT } = displayFilters

	function handleCheckBox(e) {
		setDisplayFilters({
			...displayFilters,
			[e.target.name]: e.target.checked === true ? "Yes" : "No",
		})
	}

	React.useEffect(() => {
		setIsChecked(AcceptPT === "Yes" ? true : false)
	}, [AcceptPT])

	return (
		<>
			<div>
				<label className={labelStyle} htmlFor='AcceptPT'>
					Accepting New Patients
					<input
						className={checkboxStyle}
						checked={isChecked}
						id='AcceptPT'
						name='AcceptPT'
						onChange={handleCheckBox}
						type='checkbox'
					/>
				</label>
			</div>
		</>
	)
}
