/** @format */
import { useState, useEffect } from "react"

export function useFindXX(found, option) {
	const [options, setOptions] = useState([])
	const [optionProvs, setOptionProvs] = useState([])

	useEffect(() => {
		found && setOptionProvs(found?.filter((provObj) => provObj[option] !== ""))
	}, [found])

	useEffect(() => {
		setOptions(optionProvs?.map((prov) => prov[option]))
	}, [optionProvs])

	let removeDuplicates = Array.from(new Set(options))
	let matchingProviders = [...optionProvs]
	let matchingProvidersCount = options.length

	return [removeDuplicates, matchingProviders, matchingProvidersCount]
}
