/** @format */

import React from "react"

import { FaUserMd } from "react-icons/fa"

export function ProvName({
	Name,
	updateProp,
	ProviderDetails,
	handleUpdateProviderDetails,
}) {
	return (
		<>
			{!updateProp ? (
				<p className=' title is-size-5 pt-1 '>
					{Name} <FaUserMd />
				</p>
			) : (
				<>
					<label>
						<FaUserMd /> Provider Name
						<input
							value={ProviderDetails?.Name || Name}
							name='ProvName'
							onChange={handleUpdateProviderDetails}
							className=' title is-size-5 pt-1 '
						/>
					</label>
				</>
			)}
		</>
	)
}
