/** @format */
import React, { useState } from "react";
import Header from "./components/header";
import { provList as exampleProvList } from "./utils/exampleProvList.js";
import { facilitiesLIST } from "./utils/exampleFacilitiesLIST.js";
import Main from "./components/main";
import Footer from "./components/footer";
import { useMemo } from "react";

function App() {
  const [specialties, setSpecialties] = useState();
  const [headerValue, setHeaderValue] = useState("");
  const [{ SearchResults }] = exampleProvList;
  const searchResults = exampleProvList[0].SearchResults;
  let specsHash = {};

  let specialtiesHASH = useMemo((searchResults) =>
    searchResults?.map(
      (prov) =>
        (specialtiesHASH[prov.specialty] = !specialtiesHASH[prov.specialty])
          ? 1
          : prov.specialty++,
      [searchResults]
    )
  );

  React.useEffect(() => {
    exampleProvList && console.log({ exampleProvList, specialtiesHASH });
  }, [specialtiesHASH, searchResults]);

  searchResults.map(
    (prov, idx) =>
      (specsHash[prov.Specialty] =
        typeof specsHash[prov.Specialty] === "number"
          ? specsHash[prov.Specialty] + 1
          : 1)
  );
  let specsLIST = Object.keys(specsHash);
  specsLIST = specsLIST.map((spec, idx) => ({
    Text: spec,
    ID: `${spec}_${idx}`
  }));

  console.log({ specsLIST });
  console.log({ searchResults, SearchResults });
  console.log({ exampleProvList, specialtiesHASH });

  console.log({ specsHash, specsLIST });
  return (
    <div>
      <Header headerValue={headerValue} />
      <Main
        setHeaderValue={setHeaderValue}
        searchResults__mock={searchResults}
        specsHash={specsHash}
        specsLIST={specsLIST}
        facsLIST={facilitiesLIST}
      />
      <Footer />
    </div>
  );
}

export default App;
