/** @format */
import { useState, useEffect } from "react"

export function useActiveDisplayFilters(displayFilters) {
	const [activeDisplayFilters, setActiveDisplayFilters] = useState({})
	let tempHash = {}
	// copy over any filters that aren't null or empty
	useEffect(() => {
		displayFilters &&
			Object.entries({
				...displayFilters,
			}).map(([k, v]) => {
				if (v !== "") {
					tempHash[k] = v
				}
			})
		setActiveDisplayFilters(tempHash)
	}, [displayFilters])

	return [activeDisplayFilters]
}
