/** @format */

import { useEffect } from "react";
import { useGeocodeAddress } from "../../utils/useGeocodeAddress";
import { useStaticMapBox } from "../../utils/useStaticMapBox";
import { useLocation } from "react-router-dom";
import FAQPopup from "../../components/faq-popup";
import {
  SpecialistPopup,
  PcpPopup,
  AccreditationPopup,
  BoardCertificationPopup,
  GenderPopup,
  LanguagePopup,
  NetworkPlanPopup,
  WhatsMyPlanPopup
} from "../../components/popups";
export default function ProviderDetails({ provider, setHeaderValue }) {
  const l = useLocation();

  let {
    Name,
    AcceptPT,
    Accred,
    AccredDate,
    Address1,
    Address2,
    AddressType,
    BoardCert,
    City,
    County,
    CulturalTrainingDate,
    DateCreated,
    FLang,
    Gender,
    Hosp,
    ID,
    LastName,
    LastUpdatedDate,
    MedGrp,
    Name2,
    NotHandicapAccessible,
    OHMedicaid,
    PAR,
    Participate,
    Phone,
    Provider,
    Restrictions,
    Section,
    Specialty,
    State,
    WVMedicaid,
    Website,
    WvuTier,
    WvuhMEdicareTier,
    Zip
  } = l.state.provider;

  let tempDate = DateCreated.trim("/");

  const [latLon] = useGeocodeAddress(`${Address1}, ${City}, ${State} ${Zip}`);

  const [pointMap] = useStaticMapBox(latLon);

  useEffect(() => {
    setHeaderValue("Provider Details");
  }, []);
  return (
    <div className="container">
      <section className="section">
        <div style={{ display: "flex" }}>
          <div style={{ width: "30%", minWidth: 300 }}>
            <div>
              <strong>
                Name
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{Name}</p>
            </div>
            <div>
              <strong>
                Specialty <SpecialistPopup linkTextProp="" />
              </strong>
              <p>{Specialty}</p>
            </div>
            <div>
              <strong>
                Phone
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{Phone}</p>
            </div>
            <div>
              <strong>
                Location
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <address>
                {Address1}
                {Address2 && Address2}
                <br />
                {City}, {State} {Zip}
                <br />
                {County && County} County
              </address>
            </div>
            <div>
              <strong>
                Accepting New Patients{" "}
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{AcceptPT}</p>
            </div>
          </div>

          <div style={{ width: "30%", minWidth: 300 }}>
            <div>
              <strong>
                Handicap Accessible
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{NotHandicapAccessible === "false" ? "Yes" : "No"}</p>
            </div>
            <div>
              <strong>
                Gender
                <GenderPopup />
              </strong>
              <p>{Gender}</p>
            </div>
            <div>
              <strong>
                Spoken Languages
                <LanguagePopup />
              </strong>
              <p>{"ENGLISH, " + FLang || "n/a"}</p>
            </div>
            <div>
              <strong>
                Board Certifications
                <BoardCertificationPopup />
              </strong>
              <p>{BoardCert || "n/a"}</p>
            </div>
            <div>
              <strong>
                Participating in
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{Participate || "n/a"}</p>
            </div>
          </div>
          <div style={{ width: "30%", minWidth: 300 }}>
            <fieldset>
              <strong>
                <legend>Affiliations</legend>
              </strong>

              <div>
                <em>
                  Hospital Affiliations
                  {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
                </em>
                <p>-{Hosp || "n/a"}</p>
              </div>
              <div>
                <em>
                  Medical Group Affiliations
                  {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
                </em>

                <p>-{MedGrp || "n/a"}</p>
              </div>
            </fieldset>
            {Website && (
              <div>
                <strong>
                  website
                  {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
                </strong>
                <p>{Website}</p>
              </div>
            )}
            <div>
              <strong>
                Cultural Compentency Training
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{CulturalTrainingDate || "n/a"}</p>
            </div>
            <div>
              <strong>
                Provider Type
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{Provider || "n/a"}</p>
            </div>
            {Restrictions && (
              <div>
                <strong>
                  Restrictions
                  {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
                </strong>
                <p>{Restrictions}</p>
              </div>
            )}{" "}
            <div>
              <strong>
                Last Updated Date:
                {/* <FAQPopup contentTextProp="" linkTextProp="" /> */}
              </strong>
              <p>{LastUpdatedDate ?? new Date().setUTCSeconds(DateCreated)}</p>
              <p>{LastUpdatedDate ? LastUpdatedDate : ""}</p>
            </div>
          </div>
        </div>
      </section>
      <section className="section">
        {pointMap && (
          <img
            alt={`static Mapbox map of the ${
              Address1 + "," + City + "," + State
            } area`}
            src={pointMap}
            // src={map.config.url}
            title={`static Mapbox map of the ${
              Address1 + "," + City + "," + State
            } area`}
          />
        )}
      </section>
    </div>
  );
}

