/** @format */

import { Fragment, useState, useEffect } from "react";
import { useHistory, Link } from "react-router-dom";
import ResultsCount from "./resultscount";
import { ProviderCard } from "../providercard/ProviderCard.js";
import { useFilterDisplayedResults } from "../../../utils/useFilterDisplayedResults";
import { usePagination } from "../../../utils/usePagination.js";
import Pagination from "../../../components/pagination";
import { SkeletonArticle } from "../../../components/skeletons/SkeletonArticle";
export default function Results({
  displayFilters,
  found,
  handleSaveProv,
  saved,
}) {
  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const [displayed] = useFilterDisplayedResults(found, displayFilters);
  const [height, setHeight] = useState();
  const [windowSize, setWindowSize] = useState({
    width: undefined,
    height: undefined,
  });
  const [currentTab, setCurrentTab] = useState("results");
  function toggleCurrentTab() {
    setCurrentTab(currentTab === "results" ? "saved" : "results");
  }
  const [page, setPage] = useState(1);
  const [paginatedProvs] = usePagination(
    displayed,
    Math.floor(windowSize?.height / 300) || 4
  );
  const [previewSkeletonsNum, setPreviewSkeletonsNum] = useState([
    "",
    "",
    "",
    "",
  ]);

  const [pageOfProvs, setPageOfProvs] = useState(paginatedProvs[page]);

  useEffect(() => {
    function handleResize() {
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
        roundedHeight: Math.floor(window.innerHeight / 300),
      });
    }
    handleResize();
    window.addEventListener("resize", handleResize());
    return () => {
      window.removeEventListener("resize", handleResize());
    };
  }, []);

  useEffect(() => {
    windowSize?.height &&
      setPreviewSkeletonsNum(new Array(windowSize.roundedHeight).fill(""));
  }, [windowSize]);

  useEffect(() => {
    paginatedProvs && setPageOfProvs(paginatedProvs[page || 0]);
  }, [page, paginatedProvs, windowSize, displayed]);

  useEffect(() => {
    paginatedProvs && setPageOfProvs(paginatedProvs[0]);
  }, [displayed]);

  useEffect(() => {
    paginatedProvs && setLoading(true);
    setTimeout(() => {
      setLoading(false);

      setPageOfProvs(paginatedProvs[0]);
    }, 2000);
  }, [displayed]);

  function routeToSingleProvider(event, provider) {
    event.preventDefault();
    history.push(`/providerdetails/${provider.ID}`, { provider });
  }

  return (
    <div
      className="panel section"
      style={{ display: "flex", flexFlow: "column", width: "70%" }}
    >
      <div className="tabs tabs-link-active-color-success is-boxed is-large is-centered  ">
        <ul>
          <li
            className={`    ${
              currentTab == "results" ? " is-active has-text-success-dark " : ""
            }`}
            onClick={() => setCurrentTab("results")}
          >
            <a>
              <span>Results</span>
            </a>
          </li>

          <li
            className={`  ${
              currentTab == "saved" ? " is-active has-text-success-dark " : ""
            }`}
            onClick={() => setCurrentTab("saved")}
          >
            <a>
              <span>Saved</span>
            </a>
          </li>
        </ul>
      </div>
      <ResultsCount
        found={found.length}
        saved={saved.length}
        displayed={displayed.length}
        toggleCurrentTab={toggleCurrentTab}
        setCurrentTab={setCurrentTab}
      />
      {currentTab === "saved" &&
        saved.length > 0 &&
        saved?.map((prov, idx) => {
          return (
            <Fragment key={`${prov.ID}_${idx}`}>
              <ProviderCard
                provider={prov}
                idx={idx}
                routeToSingleProvider={routeToSingleProvider}
                handleSaveProv={handleSaveProv}
              />
            </Fragment>
          );
        })}
      {currentTab === "saved" && saved.length == 0 && (
        <div className="section">
          Saved Providers Will Appear Here ... Save a Provider to add it to this
          Printable List.
        </div>
      )}
      {currentTab === "results" && (
        <div>
          {!loading && displayed && pageOfProvs
            ? pageOfProvs?.map((prov, idx) => {
                return (
                  <Fragment key={`${prov.ID}_${idx}`}>
                    <ProviderCard
                      provider={prov}
                      idx={idx}
                      routeToSingleProvider={routeToSingleProvider}
                      handleSaveProv={handleSaveProv}
                    />
                  </Fragment>
                );
              })
            : ""}

          {loading &&
            previewSkeletonsNum.map((card, idx) => (
              <SkeletonArticle key={card + "_" + idx}></SkeletonArticle>
            ))}
          {!loading &&
            displayed.length === 0 &&
            previewSkeletonsNum.map((card, idx) => <ProviderCard />)}
        </div>
      )}{" "}
      {/* 
				<div id='searchedProvs'>
					{page - 1 > 0 && (
						<button onClick={() => setPage(page - 1)}> back </button>
					)}
					<p>
						page {paginatedProvs.length !== 0 ? page + 1 : 0} of{" "}
						{paginatedProvs.length}
					</p>
					{page + 1 < paginatedProvs.length && (
						<button onClick={() => setPage(page + 1)}> more </button>
					)}
				</div>
			*/}
      <Pagination
        setPage={setPage}
        page={page}
        lastPage={paginatedProvs.length - 1}
      />
      {console.log({ page, paginatedProvs, pageOfProvs })}
    </div>
  );
}
