/** @format */

import GenderPopup from "../../../components/popups/gender"
import { labelStyle, checkboxStyle } from "../../../utils/styles"

export default function Gender({ displayFilters, setDisplayFilters }) {
	function handleChange(e) {
		setDisplayFilters({ ...displayFilters, [e.target.name]: e.target.value })
	}
	return (
		<>
			<section id='Gender' value={displayFilters.Gender}>
				<label className={labelStyle} htmlFor='Gender'>
					Gender <GenderPopup />
				</label>
				<div>
					<label className={labelStyle}>
						Female
						<input
							className={checkboxStyle}
							onChange={handleChange}
							type='radio'
							name='Gender'
							label='Female'
							value='Female'
							id='Female'
							checked={displayFilters.Gender === "Female"}
						/>
					</label>
					<label className={labelStyle}>
						Male
						<input
							className={checkboxStyle}
							onChange={handleChange}
							type='radio'
							name='Gender'
							value='Male'
							label='Male'
							id='Male'
							checked={displayFilters.Gender === "Male"}
						/>
					</label>

					<label className={labelStyle}>
						Any
						<input
							className={checkboxStyle}
							onChange={handleChange}
							type='radio'
							name='Gender'
							value=''
							label='Any'
							id='Any'
							checked={displayFilters.Gender === ""}
						/>
					</label>
				</div>
			</section>
		</>
	)
}
