/** @format */
import { BsFillCaretDownFill, BsCaretDownFill } from "react-icons/bs"
import { selectStyles } from "../../../utils/styles.js"

import { useFindXX } from "../../../utils/useFindXX"

export default function Accred({
	displayFilters,
	setDisplayFilters,
	found,
	children,
}) {
	const [accreds, accredProvs, accredProvsCount] = useFindXX(found, "Accred")

	function handleChangeDisplayFilters(e) {
		setDisplayFilters({
			...displayFilters,
			[e.target.name]: e.target.value,
		})
	}
	return (
		<>
			<select className={selectStyles} onChange={handleChangeDisplayFilters}>
				<option>Accreditation {accredProvsCount || ""}</option>
				{accreds &&
					accreds?.map((accred, idx) => (
						<option
							title={accred}
							key={`${accred}_${idx}`}
							value={accred}
							name='Accred'
							style={{ display: "flex", justifyContent: "space-between" }}
						>
							{accred.trim("")}

							{accredProvs.filter((p) => p.Accred === accred).length}
						</option>
					))}
			</select>
		</>
	)
}
