/** @format */
import { useState } from "react"
import ByLocationDisplay from "./bylocation.display"
import ByLocationInputs from "./bylocation.inputs"
import ByNameButton from "./byname.button"
import ByNameInput from "./byname.input"
import AutocompleteSearch from "../../../utils/autocompleteSearch"
import { HiLocationMarker } from "react-icons/hi"
import "../index.css"
import { css } from "@emotion/css"

const searchLocationInputStyles = css`
	background: lightgreen;
	display: flex;
	grid-gap: 3vw;
	gap: 3vw;
	padding: 1vw 2vw;
	section {
		display: flex;
		align-items: center;
		width: 100%;
		gap: 0vw;
		.searchLocation_inputsOuterBox {
			align-items: center;
			display: flex;
			width: 100%;
			padding: 0;
			height: 3rem;
			margin: 1rem;

			.searchLocation_inputsInnerBox {
				align-items: center;
				display: flex;
				font-size: 2rem;
				select#searchRadius {
					font-size: 1.6rem;
					height: 2rem;
					width: 5rem;
					text-align: center;
				}
				label {
					gap: 1vw;
					margin: 0;
				}
			}
		}
	}
`
export default function Search({
	address,
	searchFilters,
	setSearchFilters,
	submitSearch,
}) {
	let { address1, city, state, zip, radius, provname, provtype } = searchFilters
	// let location =address `${city ?? ""} ${state ?? ""} ${zip ?? ""}`

	const [locationSearchType, toggleLocationSearchType] = useState(true)

	function toggleSearchType() {
		toggleLocationSearchType(!locationSearchType)
	}

	function handleChangeQuery(e) {
		setSearchFilters({ ...searchFilters, [e.target.name]: e.target.value })
	}

	return (
		<section>
			<form onSubmit={submitSearch}>
				<div className='searchName'>
					<ByNameInput
						handleChangeQuery={handleChangeQuery}
						provname={provname}
					/>
					<ByNameButton submitSearch={submitSearch} />
				</div>
				<div>
					<div className={searchLocationInputStyles}>
						<section>
							<div className='searchLocation_icon'>
								<span role='presentation'>
									<HiLocationMarker
										onClick={toggleSearchType}
										style={{
											color: "white",
											width: 50,
											height: "80%",
										}}
									/>
								</span>
							</div>
							{locationSearchType ? (
								<div className='searchLocation_inputsOuterBox'>
									<AutocompleteSearch />
									<div className='searchLocation_inputsInnerBox'>
										<span role='presentation'>&nbsp;within&nbsp;</span>
										<select
											placeholder='10'
											id='searchRadius'
											name='radius'
											value={radius}
											onChange={handleChangeQuery}
										>
											{["5", "10", "15", "20", "25", "50", "100"].map(
												(int, idx) => (
													<option key={`${idx}`} value={int}>
														{int}
													</option>
												)
											)}
										</select>
										<label
											htmlFor='searchRadius'
											aria-label='search radius in miles'
										>
											&nbsp;miles
										</label>
									</div>
								</div>
							) : (
								<ByLocationInputs
									handleChangeQuery={handleChangeQuery}
									address1={address1}
									city={city}
									state={state}
									zip={zip}
									radius={radius}
								/>
							)}
						</section>
					</div>
					<div className='searchLocation--display'>
						<ByLocationDisplay radius={radius} location={address} />
					</div>
				</div>
			</form>
		</section>
	)
}
